.colors-and-type {
  display: grid;
  grid-template-columns: 48px 72px 281px 415px 436px 128px;
  grid-template-rows: 48px 67px 321px 289px;
  grid-auto-flow: column;
  grid-template-areas:
    "logo . . . . ."
    ". . . . . ."
    ". . pallete . type ."
    ". . pallete . . .";
  max-width: 100%;
  margin-bottom: 115px;

  &__logo-link {
    grid-area: logo;
  }
  
  &__pallete { 
    grid-area: pallete;
  }

  &__type { 
    grid-area: type; 
  }
}




